<template>
<div>
	<div class="tableSelect">
        <select v-model="selYears" id="required">
              <option v-for="option1 in options1" :value="option1.value" @click="selYearsBtn">
                  {{ option1.text }}
              </option>
            </select>
            <select v-model="selGrade" id="required">
              <option v-for="option3 in options3" :value="option3.value" @click="selGradeBtn">
                  {{ option3.text }}
              </option>
            </select>
            <select v-model="selCourse" id="required">
              <option v-for="option2 in options2" :value="option2.value" @click="selCourseBtn">
                  {{ option2.text }}
              </option>
            </select>
            <select v-model="selClass">
              <option v-for="option4 in options4" :value="option4.value" @click="selClassBtn">
                  {{ option4.text }}
              </option>
            </select>
            <button class="am-btn am-btn-success am-radius" v-on:click="findBtn"></button>
            <button class="am-btn am-btn-success am-radius" v-on:click="exportBtn"></button>
            
            <!-- <Upload action="//jsonplaceholder.typicode.com/posts/">
                <Button type="ghost" id="uploadBtn"></Button>
            </Upload> -->
      </div>

	<div id="makeupList">
		<div class="makeupListBody">
			<div class="needMakeupList">
				<span>*</span>
				<div class="list1">
					<table class="normalTable">
						<thead>
							<tr>
								<td></td>
								<td></td>
								<td></td>
								<td></td>
								<td></td>
								<td></td>
								<td></td>
							</tr>
						</thead>
						<tbody>
							<tr v-for="data in needMakeupList">
								<td v-text="data.stuNum"></td>
								<td v-text="data.stuName"></td>
								<td v-text="data.major"></td>
								<td v-text="data.year"></td>
								<td v-text="data.grade"></td>
								<td v-text="data.courseName"></td>
								<td v-text="data.testScore"></td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>

			<div class="doubleFailList">
				<span>*</span>
				<div class="list2">
					<table class="operationTable">
						<thead>
							<tr>
								<td>
									<input id="checkAll" type="checkbox" @click="checkAll()">
								</td>
								<td width="23%"></td>
								<td width="23%"></td>
								<td width="25%"></td>
								<td width="21%"></td>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(data, index) in applyStuList" :id="checkSingle" :key="data">
								<td>
									<input type="checkbox" id="checkOne">
								</td>
								<td v-text="data.teacher"></td>
								<td v-text="data.course"></td>
								<td v-text="data.time"></td>
								<td>
									<img class="rightImg" width="25px" height="25px">
									<img class="wrongImg" width="25px" height="25px">
									<!-- <ul> -->
										<!-- <span class="rightImg" width="25px" height="25px" @click="rightBtn(index)"></span>
										<span class="wrongImg" width="25px" height="25px" @click="wrongBtn(index)"></span> -->
									<!-- </ul> -->
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
            <button class="am-btn am-btn-success am-radius"></button>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'makeupList',
	data () {
		return {
			// 
			options1: [
		        { text: '()' },
		        { text: '', value: '1' },
		        { text: '', value: '2' }
		    ],
		    options2: [
		        { text: '()' },
		        { text: '', value: '1' },
		        { text: '', value: '2' }
		    ],
		    options3: [
		        { text: '()' },
		        { text: '2016-2017', value: '1' },
		        { text: '2016-2017', value: '2' }
		    ],
		    options4: [
		        { text: '' },
		        { text: '010203', value: '1' },
		        { text: '010203', value: '2' }
		    ],

			single: 'false',	// 
			needMakeupList: [
				{},
				{},
				{},
				{}
			],
			applyStuList: [
				{teacher: '', course: '', time: '2016.08.01'},
				{teacher: '', course: '', time: '2016.08.02'},
				{teacher: '', course: '', time: '2016.08.02'},
				{teacher: '', course: '', time: '2016.08.02'}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../eduMakeupExamAdmin.php').then(function (response) {
			console.log(response);
			this.needMakeupList = response.body.needMakeupList;
			this.applyStuList = response.body.applyStuList;
		});
	},
  	methods: {
  		findBtn: function () {	// 

	    },
	    exportBtn: function () {	// 

	    },

  		// 
  		checkAll: function () {
  			var inputAll = document.getElementById("checkAll");
  			var checkOne = document.getElementById("checkOne");
  			// var checkSingle = document.getElementById("checkSingle");
            // var input = checkSingle.getElementsByTagName("input");
  			var checkNum = 0;
  			for (var i = 0; i < needMakeupList.length; i++) {
  				if (this.checkOne[i].checked = true) {
  					checkNum++;
  				}
  			}
  			if (checkNum == needMakeupList.length) {
  				inputAll.checked = true;
  			}
  			else if (checkNum != needMakeupList.length) {
  				inputAll.checked = false;
  			}
  			else if (inputAll.checked = true) {
  				for (i = 0; i < needMakeupList.length; i++) {
	  				this.checkOne[i].checked = true;
	  			}
  			}
	
  		},
  		rightBtn: function (index) {
  			var r = confirm("");
  			if (r==true) {
  				alert("");
  				this.applyStuList.splice(index, 1);
  			}
  		},
  		wrongBtn: function (index) {
  			var w = confirm("");
  			if (w==true) {
  				this.applyStuList.splice(index, 1);
  			}
  		}
  	}
}
</script>

<style>
/*#selectDiv {
	display: flex;
	text-align: center;
	justify-content: center;
}*/

#makeupList {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
	position: relative;
	display: inline-block;
	overflow: hidden;
}
.makeupListBody {
	margin: 1rem 5rem;
	padding: 1.4rem 0;
	background-color: white;
}
.makeupListBody button {
	width: 5.6rem;
}
.makeupListBody span {
	float: left;
	padding-left: 2rem;
}

#required {
	background: url(../../../../components/public/images/star.png) 2% 50% no-repeat;
}

.list1, .list2 {
	padding: 2rem;
}
.list2 {
	padding-bottom: 0.8rem;
}
.doubleFailList table {
	border: 2px solid #ececec;
	width: 100%;
}

ul {
	list-style: none;
	text-align: center;
	margin: 0;
	width: 100%;
}
li {
	float: left;
	margin: 0 5%;
	cursor: pointer;
}
.rightImg {
	margin: 0 5%;
	border: 0;
	vertical-align: text-bottom;
	background: url(./images/right.png) 50% 50% no-repeat;
	cursor: pointer;
}
.wrongImg {
	margin: 0 5%;
	vertical-align: text-bottom;
	background: url(./images/wrong.png) 50% no-repeat;
	cursor: pointer;
}
.rightImg:hover {
	background: url(./images/right-on.png) 50% no-repeat;
}
.wrongImg:hover {
	background: url(./images/wrong-on.png) 50% no-repeat;
}



</style>



