<template>
<div>
	<div class="tableSelect">
		<select id="required" @change="">
			<option value="1" disabled selected>()</option>
			<option value="2"></option>
			<option value="3"></option>
		</select>
		<select>
			<option value="1" disabled selected></option>
			<option value="2">2016-2017</option>
			<option value="3">2016-2017</option>
		</select>
		<select id="required" @click="">
			<option value="1" disabled selected>()</option>
			<option value="2"></option>
			<option value="3"></option>
		</select>
		<select id="required">
			<option value="1" disabled selected>()</option>
			<option value="2"></option>
			<option value="3"></option>
		</select>
		<span class="inputNumber">
			<input v-model="inputNum" id="inputNum" placeholder="">
		</span>
		<button class="am-btn am-btn-success am-radius"></button>
		<button class="am-btn am-btn-success am-radius" v-on:click="exportBtn"></button>
	</div>

	<div id="gradeContent">
		<div class="gradeContentBody">
			<table class="normalTable">
				<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in gradeStat">
						<td v-text="data.stuNum"></td>
						<td v-text="data.stuName"></td>
						<td v-text="data.stuGrade"></td>
						<td v-text="data.stuMajor"></td>
						<td v-text="data.stuSemester"></td>
						<td v-text="data.stuCourse"></td>
						<td v-text="data.stuScore"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'gradeContent',
	data () {
		return {
			gradeStat: [
				{stuNum: '20142201010', stuName: '', stuGrade: '', stuMajor: '', stuSemester: '2016-2017', stuCourse: '', stuScore: '80'},
				{stuNum: '20142201010', stuName: '', stuGrade: '', stuMajor: '', stuSemester: '2016-2017', stuCourse: '', stuScore: '80'},
				{stuNum: '20142201010', stuName: '', stuGrade: '', stuMajor: '', stuSemester: '2016-2017', stuCourse: '', stuScore: '80'}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../eduGradeQuery.php').then(function (response) {
			console.log(response);
			this.gradeStat = response.body.gradeStat;
		});
	}
}
</script>

<style>
#gradeContent {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
}
.gradeContentBody {
	padding: 1rem 5rem;
}
/*.gradeContentBody table {
	margin-bottom: 1.6rem;
}*/

</style>



