<template>
<div>
	<div class="curSettings">
      <div>
        <span>{{year}}{{semester}}</span>
        <span>{{week}}</span>
        <span>21700</span>
      </div>
      <div>
        <span>{{teacher}}</span>
        <span>{{teacher}},{{year}}{{semester}}{{courseNum}}/</span>
      </div>
    </div>

	<div id="adjShow">
		<!--  -->
		<div class="adjShowDiv">
			<table class="applyTable">
				<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(data, index) in courseList" :key="data">
						<td class="applyAdj">
							<a value="toggle" v-on:click="applyAdjBtn(index)"></a>
						</td>
						<td v-text="data.num"></td>
						<td v-text="data.courseId"></td>
						<td v-text="data.courseName"></td>
						<td v-text="data.className"></td>
						<td v-text="data.teacherName"></td>
					</tr>
				</tbody>
			</table>
		</div>

		<!--  -->
		<div class="adjSel" v-if="isShow">
		    <table>
				<tbody>
					<tr>	<!--  -->
						<td class="designation" width="8%">
							<span></span>
						</td>
						<td class="choice" width="17%">
							<select v-model="selPreWeek">
								<option disabled></option>
								<option v-for="preWeek in weekset" :value="preWeek.text">{{ preWeek.text }}</option>
							</select>
						</td>
						<td class="designation" width="8%">
							<span></span>
						</td>
						<td class="choice" width="17%">
							<select v-model="selPerDay">
								<option disabled></option>
								<option v-for="preDay in weekdayset" :value="preDay.text">{{ preDay.text }}</option>
							</select>
						</td>
						<td class="designation" width="8%">
							<span></span>
						</td>
						<td class="choice" width="17%">
							<select v-model="selPreSection">
								<option disabled></option>
								<option v-for="preSection in lessonNumset" :value="preSection.text">{{ preSection.text }}</option>
							</select>
						</td>
						<td class="designation" width="8%">
							<span></span>
						</td>
						<td class="choice" width="17%">
							<select v-model="selClassroom">
								<option disabled></option>
								<option v-for="classroom in selectiveClassroomSet" :value="classroom.text">{{ classroom.text }}</option>
							</select>
						</td>
					</tr>
					<tr>	<!--  -->
						<td class="designation">
							<span></span>
						</td>
						<td class="choice">
							<select v-model="selAdjWeek">
								<option disabled></option>
								<option v-for="adjWeek in selectiveWeekSet" :value="adjWeek.text">{{ adjWeek.text }}</option>
							</select>
						</td>
						<td class="designation">
							<span></span>
						</td>
						<td class="choice">
							<select v-model="selAdjDay" @click="selectiveWeekday()">
								<option disabled></option>
								<option v-for="adjDay in selectiveWeekday" :value="adjDay.text">{{ adjDay.text }}</option>
							</select>
						</td>
						<td class="designation">
							<span></span>
						</td>
						<td class="choice">
							<select v-model="selAdjSection" @click="selectiveLessonNum()">
								<option disabled></option>
								<option v-for="adjSection in selectiveLessonNum" :value="adjSection.text">{{ adjSection.text }}</option>
							</select>
						</td>
						<td class="designation">
							<span></span>	<!-- 50 -->
						</td>
						<td class="choice" width="14%">
							<div style="width:10rem;">
								<Input v-model="message" type="textarea" :rows="2"></Input>
							</div>
						</td>
					</tr>
				</tbody>
		    </table>
		    <div class="submit">
		    	<button class="am-btn am-btn-success am-radius" @click="submitItBtn()"></button>
		    	<button class="am-btn am-btn-success am-radius" @click="cancelBtn()"></button>
		    </div>
		</div>

		<!--  -->
		<div class="haveAppliedInfo">
			<table class="applyTable">
				<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in applicationList">
						<td v-text="data.num"></td>
						<td v-text="data.courseId"></td>
						<td v-text="data.courseName"></td>
						<td v-text="data.teacherName"></td>
						<td v-text="data.className"></td>
						<td v-text="data.lessonChangeInfo"></td>
						<td v-text="data.useClassroom"></td>
						<td v-text="data.mediationReason"></td>
						<td v-text="data.auditType"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>
 
<script>
export default {
	data () {
		return {
			year: '',
			semester: '',
			week: '',
			teacher: '',
			courseNum: '',

			isShow: false,
			//	
			courseList: [
				{title: '', num: '01', className: '20159+31', courseName: '',
          courseId: '10301', teacherName: '61742'}
			],
			//	
			applicationList: [
				// {num: '01', courseNum: '8854934', courseName: '', courseTeacher: '', className: '20159+31', appliedTime: '', appliedClassroom: '202', appliedReason: '', applyState: ''}
			],
			// 
			message: '',
			// adjSel
	        selPreWeek: '',	// 
	        weekset: [
	          // { text: '', value: '0' },
	          // { text: '', value: '1' }
	        ],
	        selPerDay: '',		// 
	        weekdayset: [],
	        selPreSection: '',		// 
	        lessonNumset: [],
	        selClassroom: '',		// 
	        selectiveClassroomSet: [],
	        selAdjWeek: '',	// 
	        selectiveWeekSet: [],
	        selAdjDay: '',		// 
	        selectiveWeekday: [],
	        selAdjSection: '',		// 
	        selectiveLessonNum: []
		}
	},
  	beforeMount: function() {
        this.$http.post('./alternateLessionApplication.action',{},{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.year = data.year;
            this.semester = data.semester;
            this.week = data.week;
            this.teacher = data.teacher;
            this.courseNum = data.courseNum;
            this.courseList = data.courseList;
            this.applicationList = data.applicationList;
        },function(error){
            console.log("error:");
            console.log(error);
        });
    },
	methods: {
		// weekChange: function(){
		// 	alert(this.selPreWeek);
		// },
		// 
	    applyAdjBtn: function (index) {
    		if (this.isShow == false) {
    			this.isShow = true;
    			this.$http.post('./alternateLessionApplication/application-button.action',{
    				"teacherName": this.courseList[index].teacherName,
    				"courseId": this.courseList[index].courseId,
    				"courseName": this.courseList[index].courseName,
    				"className": this.courseList[index].className,
		        },{
		            "Content-Type":"application/json"
		        }).then(function(response){
		            console.log(":");
		            console.log(response.body);
		            var data = response.body;
		            if (data.result == "1") {
		            	this.weekset = data.weekset;
			            this.weekdayset = data.weekdayset;
			            this.lessonNumset = data.lessonNumset;
			            this.selectiveWeekSet = data.selectiveWeekSet;
			            this.selectiveClassroomSet = data.selectiveClassroomSet;
		            }else if (data.result == "2") {
				        alert("");
				    }else if (data.result == "0") {
				    	alert("...");
				    }
		        },function(error){
		            console.log("error:");
		            console.log(error);
		        });
    		}
    		else {
    			this.isShow = true;
    		}
    	},
		// 
		selectiveWeekday: function () {
			this.$http.post('./alternateLessionApplication/application-select.action',{
	        	"selectedWeek": this.selPreWeek,
	        	"selectedClassroom": this.selectedClassroom
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log(":");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.selectiveWeekday = data.selectiveWeekday
	            }else if (data.result == "2") {
			        alert("");
			    }else if (data.result == "0") {
			    	alert("...");
			    }
	        },function(error){
	            console.log("error:");
	            console.log(error);
        	});
		},
		// 
		selectiveLessonNum: function () {
			this.$http.post('./alternateLessionApplication/application-select.action',{
				"selectedWeek": this.selPreWeek,
	        	"selectedClassroom": this.selectedClassroom
	        },{
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log(":");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.selectiveLessonNum = data.selectiveLessonNum
	            }else if (data.result == "2") {
			        alert("");
			    }else if (data.result == "0") {
			    	alert("...");
			    }
	        },function(error){
	            console.log("error:");
	            console.log(error);
        	});
		},
    	// 
    	submitItBtn: function () {
    		if (this.isShow ==true) {
    			this.isShow = false;
    			this.$http.post('./alternateLessionApplication/application-submit.action',{
					"originWeek": this.selPreWeek,
		        	"originWeekday": this.selPerDay,
		        	"originLessonNum": this.selPreSection,
		        	"selectedClassroomId": this.selClassroom,
		        	"selectedWeek": this.selAdjWeek,
		        	"selectedWeekday": this.selAdjDay,
		        	"selectedLessonNum": this.selAdjSection,
		        	"mediationReason": this.message
		        },{
		            "Content-Type":"application/json"
		        }).then(function(response){
		            console.log(":");
		            console.log(response.body);
		            var data = response.body;
		            if (data.result == "1") {
		            	this.applicationList = data.applicationList
		            }else if (data.result == "2") {
				        alert("");
				    }else if (data.result == "0") {
				    	alert("...");
				    }
		        },function(error){
		            console.log("error:");
		            console.log(error);
	        	});
    		}
    		else {
    			this.isShow = false;
    		}
    	},
    	cancelBtn: function () {
    		this.isShow = false;
    	}
  	}
}
</script>

<style>
#adjShow {
    text-align: center;
    margin: 1rem 0;
}
.adjShowDiv {
	background-color: white;
}

.applyAdj a {
  color: #2c3e50;
  text-decoration: underline;
  cursor: pointer;
}
.applyAdj a:link,
.applyAdj a:hover {
	color: black;
}
.applyAdj a:active {
	color: blue;
}

/**/
.adjSel {
    text-align: center;
    margin: 0.5rem;
}
.adjSel table {
	width: 100%;
}
.designation {
	text-align: right;
}
.choice {
	text-align: left;
}
/*.choice select {
	border-radius: 1rem;
	width: 10rem;
	height: 1.8rem;
	font-size: 0.8rem;
	outline: none;
}
select {
	cursor: pointer;
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
	background: url(../../public/images/angle-down.png) no-repeat 95% 90%;
}*/
.adjSel tr {
	height: 2.7rem;
}
.adjSel td {
	position: relative;
}
.submit {
	text-align: center;
	margin-bottom: 0.7rem;
}
.submit button {
	width: 5.6rem;
	margin: 0 0.7rem;
}
</style>



