<template>
<div>
	<div class="tableSelect">
      <input v-model="teacherName" placeholder="">
      <button class="am-btn am-btn-success am-radius" v-on:click="searchBtn"></button>
      <button class="am-btn am-btn-success am-radius" v-on:click="exportTemplateBtn"></button>
      <button class="am-btn am-btn-success am-radius buttonPart" v-on:click="exportBtn"></button>
      <button class="am-btn am-btn-success am-radius buttonPart" v-on:click="inportBtn"></button>
    </div>

	<div id="salaryBody">
		<div class="salaryContent">
			<table class="normalTable">
				<thead>
					<tr>
						<th width=""></th>
						<th width=""></th>
						<th width=""></th>
						<th width=""></th>
						<th width=""></th>
						<th width="">/</th>
						<th width=""></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in coursePay">
						<td v-text="data.teacherId"></td>
						<td v-text="data.teacherName"></td>
						<td v-text="data.jobName"></td>
						<td v-text="data.courseName"></td>
						<td v-text="data.courseHours"></td>
						<!-- <td class="textBtn"><a @click="periodBtn">{{data.period}}</a></td> -->
						<td v-text="data.coursePayroll"></td>
						<td v-text="data.totalCoursePay"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'salaryBody',
	data () {
		return {
			teacherName: '',
			coursePay: [
				{}
			]
		}
	},
	beforeMount: function() {
        this.$http.post('../eduSalary.php',{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.coursePay = data.coursePay;
        },function(error){
            console.log("error:");
            console.log(error);
        });
    },
	methods: {
		searchBtn: function () {
			this.$http.post('../eduSalary.php',{
				"teacherName": this.teacherName
			},{
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log(":");
	            console.log(response.body);
	            var data = response.body;
	            if(data.result == "1") {
                    this.coursePay = data.coursePay;
                }else if (data.result == "2") {
                    alert("");
                }else if (data.result == "0") {
                	alert("...");
                }
	        },function(error){
	            console.log("error:");
	            console.log(error);
	        });
	    },  // 
	    inportBtn: function () {

	    },  // 
	    exportBtn: function () {

	    },  // 
	    exportTemplateBtn: function () {

	    }   // 
	}
}
</script>

<style>
#salaryBody {
	width: 100%;
	background-color: #f3f3f3;
	position: relative;
	display: inline-block;
	overflow: auto;
}
.salaryContent {
	text-align: center;
	background-color: white;
	margin: 1rem 5rem;
}

</style>


