<template>
<div>
	<div class="blank">
		<div class="leftPart">
			<span>{{preSemester}}</span>
			<span>
				<span class="textBlue">{{preWeek}}</span>
			</span>
		</div>
		<div class="rightPart">
			<span></span>
			<span class="textBlue">{{warmPrompt}}</span>
		</div>
	</div>

	<div id="tchGrdPutBody">
		<div class="tchGrdPutDiv">
			<!-- <span>2016-20174</span> -->
			<div class="tchGpMain">
				<div class="tchGrdTable">
					<table class="operationTable">
						<thead>
							<tr>
								<th width="7%"></th>
								<th width="5%"></th>
								<th width="12%"></th>
								<th width="11%"></th>
								<th width="34%"></th>
								<th width="10%"></th>
								<th width="9%"></th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="data in tchGradesList">
								<td class="textBtn"><a @click="gradeInputBtn">{{data.gradeInput}}</a></td>
								<td v-text="data.number"></td>
								<td v-text="data.yearSemester"></td>
								<td v-text="data.teacher"></td>
								<td v-text="data.className"></td>
								<td v-text="data.courseName"></td>
								<td class="textBtn"><a @click="exportFormatBtn"></a></td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'tchGrdPutBody',
	data () {
		return {
			preSemester: '2016-2017',
			preWeek: '20',
			warmPrompt: '15',

			tchGradesList: [
				{gradeInput: '', number: '1', yearSemester: '2016-2017', teacher: '61742', className: '20159+31+20152A', courseName: ''},
				{gradeInput: '', number: '1', yearSemester: '2016-2017', teacher: '61742', className: '20159+31+20152A', courseName: ''},
				{gradeInput: '', number: '1', yearSemester: '2016-2017', teacher: '61742', className: '20159+31+20152A', courseName: ''},
				{gradeInput: '', number: '1', yearSemester: '2016-2017', teacher: '61742', className: '20159+31+20152A', courseName: ''}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../tchGradesInput.php').then(function (response) {
			console.log(response);
			this.preSemester = response.body.preSemester;
			this.preWeek = response.body.preWeek;
			this.tchGradesList = response.body.tchGradesList;
		});
	},
	methods: {
		gradeInputBtn: function () {
			window.location.href = "/teacher/gradesInput";
		},
		exportFormatBtn: function () {
			var r = confirm("");
			if (r==true) {
				alert("");
			}
			else {
				alert("");
			}
		}
	}
}
</script>

<style>
#tchGrdPutBody {
	background-color: #f3f3f3;
}
.tchGrdPutDiv {
	padding: 0.6rem 0;
	text-align: center;
	font-size: 1rem;
}
.tchGpMain {
	text-align: center;
	background-color: white;
	margin: 0.5rem 5rem;
	padding-bottom: 1.3rem;
	border: 1px solid #ececec;	/**/
}
.tchGrdTable {
	margin: 0.5rem 0.5rem;
	/*height: 20rem;*/
	border: 0.7rem solid transparent;
}
.tchGrdTable table {
	border-collapse: collapse;
	/*height: 3.2rem;*/
}

/*.textBtn a {
	text-decoration: underline;
	color: #3399ff;
	cursor: pointer;
}
.textBtn a:hover,
.textBtn a:focus {
	color: #5cadff;
}
.textBtn a:active {
	color: #3091f2;
}*/
</style>


