<template>
<div>
	<div class="tableSelect">
		<input v-model="courseNum" placeholder="">
		<input v-model="courseName" placeholder="">
	    <select>
			<option v-model="teacherNumber" disabled selected></option>
			<option v-for="option1 in options1" :value="option1.value" @click="tchNumClick(option1.value)">
				{{ option1.text }}
			</option>
	    </select>
	    <select>
	    	<option v-model="teacher" disabled selected></option>
			<option v-for="option2 in options2" :value="option2.value" @click="tchClick(option2.value)">
				{{ option2.text }}
			</option>
	    </select>
		<button class="am-btn am-btn-success am-radius" @click="checkTableBtn"></button>
	    <button class="am-btn am-btn-success am-radius" @click="submitBtn"></button>
	</div>

	<div id="cancelContent">
		<div class="cancelContentBody">
			<table class="operationTable">
				<thead>
					<tr>
						<th width="14%"></th>
						<th width="14%"></th>
						<th width="14%"></th>
						<th width="14%"></th>
						<th width="16%"></th>
						<th width="14%"></th>
						<th width="14%"></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(data, index) in scoreUnloadList" :key="data">
						<td v-text="data.classNum"></td>
						<td v-text="data.className"></td>
						<td v-text="data.teacherNum"></td>
						<td v-text="data.teacherName"></td>
						<td v-text="data.time"></td>
						<td v-text="data.check"></td>
						<td class="textBtn">
							<ul>
								<li><a @click="checkClick(index)"></a></li>
								<li><a @click="backoutClick(index)"></a></li>
							</ul>
						</td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	data () {
		return {
			courseNum: '',
			courseName: '',
			teacherNumber: '',
			teacher: '',
			// select
			options1: [
				// {text: '123456', value:'1'}
			],
			options2: [
				// {text: ''}
			],

			scoreUnloadList: [
				{classNum: '010203', className: '', teacherNum: '010203', teacherName: '', time: '06.01.23 12:55', check: ''}
				// {classNum: '010203', className: '', teacherNum: '010203', teacher: '', time: '06.01.23 12:55', check: ''}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../eduAdminCancelGrade.php',{
            "Content-Type":"eduAdminCancelGrade/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.scoreUnloadList = data.scoreUnloadList;
        },function(error){
            console.log("error:");
            console.log(error);
        });
	},
  	methods: {
  		tchNumClick: function (value) {
  			this.$http.post('../eduAdminCancelGrade.php', {
  				"courseNum": courseNum,
  				"courseName": courseName,
  				"value1": option1.value,
  				"value2": option2.value
  			}, {
  				"Content-Type":"eduAdminCancelGrade/json"
  			}).then(function(response){
  				console.log(":");
                console.log(response);
                var data = response.body;
                if(data.result == "1") {
                    this.scoreUnloadList = data.scoreUnloadList;
                }else{
                    alert("");
                }
  			});
  		},
  		tchClick: function () {

  		},

  		checkTableBtn: function () {

  		},
  		submitBtn: function () {

  		},

  		checkClick: function (index) {
			var r = confirm("")
			if (r==true) {
				alert("");
				this.scoreUnloadList[index].check=""
			}
  		},
  		backoutClick: function (index) {
			var r = confirm("")
			if (r==true) {
				alert("");
				this.scoreUnloadList.splice(index,1)
			}
  		}
  	}
}


</script>

<style>
#cancelContent {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
}
.cancelContentBody {
	padding: 1rem 5rem;
}
.cancelContentBody table {
	width: 100%;
	/*margin-bottom: 1.6rem;*/
	/*padding: 0.5rem 0.5rem;*/
	border-spacing: 0;
	border-collapse: separate;
	background: white;
}
.cancelContentBody button {
	width:  5.6rem;
}
</style>



