<template>
<div>
	<div class="blank">
		<div class="lpart">
			<span>{{inputLesson}}</span>
			<span>{{classes}}</span>
		</div>
		<div class="rpart">
			<span>{{teacher}}</span>
		</div>
	</div>

	<div id="gradePutBody">
		<div class="gradePutDiv">
			<span>{{ number }}</span>
			<div class="gpMain">
				<div class="submitGrade">
					<span></span>
					<span>
						<input v-model="usualRate" placeholder="20">%
					</span>
					<span>
						<input v-model="finalRate" placeholder="20">%
					</span>
					<span>
						<input v-model="finalRate" placeholder="50">%
					</span>
					<span>
						<input v-model="testRate" placeholder="10">%
					</span>
				</div>
				<div class="select-button">
					<select v-model="scoreType" @click="scoreTypeClick">
						<option value="1"></option>
						<option value="2"></option>
					</select>
					<select>
						<option value="1" disabled selected></option>
						<option value="2">2016-2017</option>
						<option value="3">2016-2017</option>
					</select>
					<select>
						<option value="1" disabled selected></option>
						<option value="2"></option>
						<option value="3"></option>
					</select>
					<!-- <select v-model="sortord">
						<option v-for="sortordOpt in sortordOpt" :value="sortordOpt.value">
							{{ sortordOpt.text }}
						</option>
					</select> -->
					<button class="am-btn am-btn-success am-radius" @click="checkBtn"></button>
					<!-- <button class="am-btn am-btn-success am-radius" @click="changeBtn"></button> -->
					<!-- <button class="am-btn am-btn-success am-radius" @click="saveBtn"></button> -->
					<button class="am-btn am-btn-success am-radius rightBtn" @click="exportBtn"></button>
					<button class="am-btn am-btn-success am-radius rightBtn" @click="submitBtn"></button>
				</div>
				<!--  -->
				<div class="scoreTable" v-if="scoreShow">
					<table class="normalTable">
						<thead>
							<tr>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(areTestScore, index) in areTestScoreList" :id="'areTestInputTr'+index" :key="areTestScore">
								<td v-text="areTestScore.stuNum"></td>
								<td v-text="areTestScore.stuName"></td>
								<td>
									<input id="input1" type="text" :value="areTestScore.usualGrade" readonly="true">
								</td>
								<td>
									<input id="input2" type="text" :value="areTestScore.midtermGrade" readonly="true">
								</td>
								<td>
									<input id="input3" type="text" :value="areTestScore.finalGrade" readonly="true">
								</td>
								<td>
									<input id="input4" type="text" :value="areTestScore.testGrade" readonly="true">
								</td>
								<td v-text="areTestScore.gradeMark"></td>
								<td>
									<img :id="'areTestEditImg'+index" width="25px" height="25px" src="../../public/images/edit.png" @click="editClick('areTest',index)">
					                <img :id="'areTestSaveImg'+index" width="25px" height="25px" class="saveImg" src="../../public/images/save.png" @click="saveClick('areTest',index)">
					                <img :id="'areTestRestoreImg'+index" width="25px" height="25px" class="restoreImg" src="../../public/images/restore.png" @click="restoreClick('areTest',index)">
					                <img :id="'areTestDeleteImg'+index" width="25px" height="25px" src="../../public/images/delete.png" @click="deleteClick('areTest',index)">
								</td>
							</tr>
						</tbody>
					</table>
				</div>
				<!--  -->
				<div class="makeupScoreTable" v-else="makeupScoreShow">
					<table class="normalTable">
						<thead>
							<tr>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(makeupScore, index) in makeupScoreList" :id="'makeupInputTr'+index" :key="makeupScore">
								<td v-text="makeupScore.stuNum"></td>
								<td v-text="makeupScore.stuName"></td>
								<td>
									<!-- <input v-text="data.makeupGrade"> -->
									<input id="input4" type="text" :value="makeupScore.makeupGrade" readonly="true">
								</td>
								<td>
									<img :id="'makeupEditImg'+index" width="25px" height="25px" src="../../public/images/edit.png" @click="editClick('makeup',index)">
					                <img :id="'makeupSaveImg'+index" width="25px" height="25px" class="saveImg" src="../../public/images/save.png" @click="saveClick('makeup',index)">
					                <img :id="'makeupRestoreImg'+index" width="25px" height="25px" class="restoreImg" src="../../public/images/restore.png" @click="restoreClick('makeup',index)">
					                <img :id="'makeupDeleteImg'+index" width="25px" height="25px" src="../../public/images/delete.png" @click="deleteClick('makeup',index)">
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'gradePutBody',
	data () {
		return {
			inputLesson: '',
			classes: '20159+31+20152A',
			teacher: '',

			scoreType: '1',
			scoreShow: true,
			makeupScoreShow: false,
			number: '82',	// 
	        // 
	        // sortord: '0',
	        // sortordOpt: [
	        // 	{ text: '', value: '0' },
	        // 	{ text: '', value: '1' },
	        // 	{ text: '', value: '2' },
	        // 	{ text: '', value: '3' },
	        // 	{ text: '', value: '4' }
	        // ],

	        // 
			areTestScoreList: [
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'},
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'},
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'},
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'},
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'},
				{stuNum: '15303010503', stuName: '', usualGrade: '98', midtermGrade: '75', finalGrade: '86.5', testGrade: '96'}
			],
			// 
			makeupScoreList: [
				{stuNum: '15303010503', stuName: '', makeupGrade: '85'},
				{stuNum: '15303010503', stuName: '', makeupGrade: '85'},
				{stuNum: '15303010503', stuName: '', makeupGrade: '85'}
			],
			usualRate:"",
			finalRate:"",
			testRate:"",
			// selectRemark: '0',
			// optionsMark: [
			// 	{ text: '', value: '0' },
			// 	{ text: '***', value: '1' },
			// 	{ text: '***', value: '2' }
			// ]
		}
	},
	beforeMount: function () {
		this.$http.post('../gradesInput.php').then(function (response) {
			console.log(response);
			this.inputLesson = response.body.inputLesson;
			this.classes = response.body.classes;
			this.teacher = response.body.teacher;
			this.areTestScoreList = response.body.areTestScoreList;
		});
	},
	methods: {
		// 
		scoreTypeClick: function () {
			console.log(this.scoreType);
			if (this.scoreType == 1) {
				this.scoreShow = true;
				this.makeupScoreShow = false;
			}
			else if (this.scoreType == 2) {
				this.scoreShow = false;
				this.makeupScoreShow = true;
			}
		},
		// 
		checkBtn: function () {

		},
		submitBtn: function () {

		},
		exportBtn: function () {

		},
		// 
		editClick: function(type,index){
            var inputTr = document.getElementById(type+"InputTr"+index);
            var input = inputTr.getElementsByTagName("input");
            var editImg = document.getElementById(type+"EditImg"+index);
            var saveImg = document.getElementById(type+"SaveImg"+index);
            var restoreImg = document.getElementById(type+"RestoreImg"+index);
            var deleteImg = document.getElementById(type+"DeleteImg"+index);
            var i = null;
			// 
            for(i = 0;i<input.length;i++){
              input[i].readOnly = false;
              input[i].style.border = "0.1rem solid #d4d4d9";
            }
			// ,
            editImg.style.display = "none";
            saveImg.style.display = "inline";
            deleteImg.style.display = "none";
            restoreImg.style.display = "inline";
        },
        // ,,
        restoreClick: function(type,index){
            if(confirm("")){
              var inputTr = document.getElementById(type+"InputTr"+index);
              var input = inputTr.getElementsByTagName("input");
              var editImg = document.getElementById(type+"EditImg"+index);
              var saveImg = document.getElementById(type+"SaveImg"+index);
              var restoreImg = document.getElementById(type+"RestoreImg"+index);
              var deleteImg = document.getElementById(type+"DeleteImg"+index);
              var i = null;
				// value,inputvaluedata,,data
               if (type == "areTest") {
                input[0].value = this.areTestScoreList[index].usualGrade;
                input[1].value = this.areTestScoreList[index].midtermGrade;
                input[2].value = this.areTestScoreList[index].finalGrade;
                input[3].value = this.areTestScoreList[index].testGrade;
               }
               if (type == "makeup") {
               	input[0].value = this.makeupScoreList[index].makeupGrade;
               }
				// 
              for(i = 0;i<input.length;i++){
                input[i].readOnly = true;
                input[i].style.border = "none";
              }
              editImg.style.display = "inline";
              saveImg.style.display = "none";
              deleteImg.style.display = "inline";
              restoreImg.style.display = "none";
            }
        },
        deleteClick: function(type,index){
			// data
			// ,data,,
            if (type == "areTest") {
            	if(confirm("")){
	              this.areTestScoreList.splice(index, 1);
	            }
            }
            else if (type == "makeup") {
            	if(confirm("")){
	              this.makeupScoreList.splice(index, 1);
	            }
            }
        },
        // 
		saveClick: function(type,index){
			if(confirm("")){
			  var inputTr = document.getElementById(type+"InputTr"+index);
			  var input = inputTr.getElementsByTagName("input");
			  var editImg = document.getElementById(type+"EditImg"+index);
			  var saveImg = document.getElementById(type+"SaveImg"+index);
			  var restoreImg = document.getElementById(type+"RestoreImg"+index);
			  var deleteImg = document.getElementById(type+"DeleteImg"+index);
			  var i = null;
			// data,inputvaluedata,,data
			   if (type == "areTest") {
			    this.areTestScoreList[index].usualGrade = input[0].value;
			    this.areTestScoreList[index].midtermGrade = input[1].value;
			    this.areTestScoreList[index].finalGrade = input[2].value;
			    this.areTestScoreList[index].testGrade = input[3].value;
			   }
			   if (type == "makeup") {
			   	this.makeupScoreList[index].makeupGrade = input[0].value;
			   }
			    // console.log(this.areTestScoreList);
			  for(i = 0;i<input.length;i++){
			    input[i].readOnly = true;
			    input[i].style.border = "none";
			  }
			// ,data,,
			  editImg.style.display = "inline";
			  saveImg.style.display = "none";
			  deleteImg.style.display = "inline";
			  restoreImg.style.display = "none";
			}
		}
	}
}
</script>

<style>
#gradePutBody {
	background-color: #f3f3f3;
}
.gradePutDiv {
	padding: 1rem 2rem;
}
.gradePutDiv span {
	margin: 0 3rem;
}
.gpMain {
	background-color: white;
	margin: 0.5rem 3rem;
	padding-bottom: 1.3rem;
}
/**/
.submitGrade {
	/*padding: 0.9rem 0;*/
	text-align: center;
	padding-top: 1rem;
	width: 100%;
	font-size: 0.9rem;
}
.submitGrade span {
	margin-left: 2rem;
	margin-right: 2rem;
}
.submitGrade input {
	width: 2.5rem;
}
/**/
.select-button {
	margin: 1rem 2rem;
}
.select-button select {
	margin-right: 1.4rem;
}
.select-button button {
	margin: 0 0.7rem;
	width: 5.6rem;
}
.rightBtn {
	float: right;
}
/**/
.scoreTable,
.makeupScoreTable {
	margin: 0.5rem 2rem;
}
.scoreTable input,
.makeupScoreTable input {
	outline:none;
    border: none;
    margin: 0;
    text-align: center;
}
.scoreTable img,
.makeupScoreTable img {
	cursor: pointer;
	margin: 0 0.3rem;
	vertical-align: bottom;
}

/**/
.saveImg{
  display: none;
}
/**/
.restoreImg{
  display: none;
}

</style>


